import React from 'react';
import Tooltip from '@mui/material/Tooltip';

function getTypes(types, totalbg) {
    let color = "";
    let mark = null;
    let feature = '';
    if (types) {
        if (types.indexOf('notrussiansign') >= 0) { color = 'notrussiansign' }
        if (types.indexOf('dialectsign') >= 0) { color = 'dialectsign' }
        if (types.indexOf('historicsign') >= 0) { color = 'oldersign' }
        if (types.indexOf('lexicsign') >= 0) { color = 'lexicosign' }
        if (types.indexOf('equivalentsign') >= 0) { mark = 'equ'; color = 'equsign' }
        if (types.indexOf('alternatesign') >= 0) { mark = 'alt'; color = 'altsign' }
        if (types.indexOf('featuresign') >= 0) { feature = 'featuresign'; mark = 'loc' }
    }
    if (totalbg) color = totalbg;
    return { color, mark, feature }
}

const SingleLetter = ({ oneletter, showUni, totalbg }) => {
    const types = getTypes(oneletter.types, totalbg);

    const showAltmark = () => {
        if (types.mark) return (<div className='markplace'><span className='altmark'>{types.mark}</span></div>)
    }

    const showUnicode = () => {
        if (showUni) return (<p className='smalltitle smallunititle'>{oneletter.unicode}</p>)
    }

    const letterTitle = showUni ? oneletter.description : oneletter.unicodes.join(', ');
    const showLetter = () => {
        return (showUni ?
            String.fromCharCode(`0x${oneletter.unicode}`) :
            oneletter.unicodes.map(alt => { return (String.fromCharCode(`0x${alt}`)) }))
    }

    return (
        <Tooltip title={letterTitle} followCursor>
            <div className={"letter " + types.color}>
                {showUnicode()}
                <p className={(showUni ? "biguniletter" : "bigletter") + " " + types.feature}>{showLetter()}</p>
                {showAltmark()}
            </div>
        </Tooltip>
    )
};

export default SingleLetter;
