import React from 'react';
import SingleLetter from './SingleLetter';

const Adds = ({ alpahabetadds, title, totalbg }) => {

    if (alpahabetadds[0]) {
        return (<>
            <p className='smalltitle'>{title}</p>
            <div className='flexcontainer'>
                {alpahabetadds.map(item => {
                    return (<SingleLetter oneletter={item} showUni={false} totalbg={totalbg} key={item.unicode} />)
                })}
            </div>
        </>)
    }
    else {
        return (<div></div>)
    }

};

export default Adds;