import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import Adds from './Adds';
import Language from './Language';
import Letters from './Letters';
import Unicodes from './Unicodes';

const Alphabet = () => {
    const [ualpahabet, setUAlpahabet] = useState([]);
    const [lalpahabet, setLAlpahabet] = useState([]);
    const [udialect, setUDialect] = useState([]);
    const [ldialect, setLDialect] = useState([]);
    const [uhistoric, setUHistoric] = useState([]);
    const [lhistoric, setLHistoric] = useState([]);
    const [ulexic, setULexic] = useState([]);
    const [llexic, setLLexic] = useState([]);
    const [uunicodegroups, setUUnicodeGroups] = useState([]);
    const [lunicodegroups, setLUnicodeGroups] = useState([]);
    const [language, setLanguage] = useState({});
    const [langnames, setLangnames] = useState([]);
    const [langgroups, setLanggroups] = useState([]);
    const { currentLang } = useParams();

    useEffect(() => {
        let curlng = currentLang ? currentLang : "Abkhazian"

        let lng = 'https://raw.githubusercontent.com/typedev/cyrillic_lib/master/langlib/output/' + curlng + '.json'
        fetch(lng)
            .then(res => res.json())
            .then(data => {
                setUAlpahabet(data.uppercase_alphabet);
                setLAlpahabet(data.lowercase_alphabet);
                setUDialect(data.uppercase_dialect);
                setLDialect(data.lowercase_dialect);
                setUHistoric(data.uppercase_historic);
                setLHistoric(data.lowercase_historic);
                setULexic(data.uppercase_lexic);
                setLLexic(data.lowercase_lexic);
                setUUnicodeGroups(data.uppercase_unicodes_list);
                setLUnicodeGroups(data.lowercase_unicodes_list);
            })
        let lng1 = 'https://raw.githubusercontent.com/typedev/cyrillic_lib/master/langlib/' + curlng + '.json'
        fetch(lng1)
            .then(res => res.json())
            .then(data => {
                setLanguage(data);
                setLangnames(data.alt_names_eng);
                setLanggroups(data.language_group);
            })
    }, [currentLang]);


    useEffect(() => {
        const LettersUp = document.querySelector('.btnup');
        const LettersDn = document.querySelector('.btndn');
        const ualphBlock = document.querySelector('.ualph');
        const lalphBlock = document.querySelector('.lalph');

        LettersDn.addEventListener('click', function () {
            ualphBlock.classList.add('hidden');
            lalphBlock.classList.remove('hidden');
            LettersDn.classList.add('togglebtnsactive');
            LettersUp.classList.remove('togglebtnsactive');
        });

        LettersUp.addEventListener('click', function () {
            ualphBlock.classList.remove('hidden');
            lalphBlock.classList.add('hidden');
            LettersUp.classList.add('togglebtnsactive');
            LettersDn.classList.remove('togglebtnsactive');
        });
    }, []);


    return (

        <div className='container'>
            <Language language={language} langnames={langnames} langgroups={langgroups} />
            <div className='infocontainer'><p className='smalltitle'></p><button className='togglebtns togglebtnsactive btnup'></button><button className='togglebtns btndn'></button></div>

            <div className='lettersblock ualph'>
                <Letters alpahabet={ualpahabet} />
                <Adds alpahabetadds={udialect} title=' ' totalbg='dialectsign' />
                <Adds alpahabetadds={uhistoric} title=' ' totalbg='oldersign' />
                <Adds alpahabetadds={ulexic} title='   ' totalbg='lexicosign' />
                <Unicodes unicodegroups={uunicodegroups} title='   ' />
            </div>

            <div className='lettersblock lalph hidden'>
                <Letters alpahabet={lalpahabet} />
                <Adds alpahabetadds={ldialect} title=' ' totalbg='dialectsign' />
                <Adds alpahabetadds={lhistoric} title=' ' totalbg='oldersign' />
                <Adds alpahabetadds={llexic} title='   ' totalbg='lexicosign' />
                <Unicodes unicodegroups={lunicodegroups} title='   ' />
            </div>

        </div>);
};

export default Alphabet;

