import React from 'react';
import SingleLetter from './SingleLetter';

const Unicodes = ({ unicodegroups, title }) => {

    return (<>
        <p className='smalltitle'>{title}</p>
        <div className='flexcontainer'>
            {unicodegroups.map(item => {
                return (<SingleLetter oneletter={item} showUni={true} key={item.unicode} />)
            })}
        </div>

    </>)
};

export default Unicodes;