import React, { useEffect, useState } from 'react';
import Popover from '@mui/material/Popover';
import Popper from '@mui/material/Popper';
import { NavLink } from 'react-router-dom';

const Langlist = () => {
    const [langlistnames, setLangListNames] = useState([]);
    const [rus, setRus] = useState(true);
    // const [anchorEl, setAnchorEl] = useState(null);

    // const handleClick = (event) => {
    //     setAnchorEl(anchorEl ? null : event.currentTarget);
    // };

    // const open = Boolean(anchorEl);
    // const id = open ? 'simple-popper' : undefined;

    useEffect(() => {
        let lng = 'https://raw.githubusercontent.com/typedev/cyrillic_lib/master/cyrillic_lib.json'
        fetch(lng)
            .then(res => res.json())
            .then(data => {
                setLangListNames(data);
            })
    }, []);

    const handleChangerus = () => {
        return setRus(!rus)
    }

    return (<>
        {/* <div><button className='langBtn' onClick={handleClick}>&#8594;</button></div>
        <Popper
            id={id}
            open={open}
            anchorEl={anchorEl}
            placement='right-start'
        > */}
        <ul className='langmenu'>
            <button className={`togglebtnsruen${rus ? ' togglebtnruensactive' : ''}`} onClick={handleChangerus}>Rus</button> <button className={`togglebtnsruen${rus ? '' : ' togglebtnruensactive'}`} onClick={handleChangerus}>ENG</button>
            {langlistnames.map(item => {
                return (
                    <li key={item.name_eng}><NavLink to={item.name_eng}>{rus ? item.name_rus : item.name_eng}</NavLink></li>
                )
            })
            }
        </ul>
        {/* </Popper> */}
    </>
    );
};

export default Langlist;

