import React from 'react';
import SingleLetter from './SingleLetter';

const Letters = ({ alpahabet }) => {
    return (

        <div className='flexcontainer'>
            {alpahabet.map(item => {
                if (!item.alts[0]) {
                    return (<SingleLetter oneletter={item} showUni={false} key={item.unicode} />)
                } else {
                    return (
                        <div className='altborder'><div className='altborder_in' key={item.unicode}>
                            <SingleLetter oneletter={item} showUni={false} />
                            {item.alts.map(itm => {
                                return (<SingleLetter oneletter={itm} showUni={false} key={itm.unicode} />)
                            })}
                        </div></div>
                    )
                }
            })}
        </div>

    )
};

export default Letters;